<template>
  <div class="register-box">
    <img src="../../assets/images/logo.png" class="logo-img" />

    <div style="padding-right: 1.143rem;">
      <van-field v-model="tel" class="w-100b colon" label="手机号" placeholder="请输入手机号" />

      <van-field v-model="password" class="w-100b colon" label="密码" placeholder="请输入密码" />
      <van-field v-model="repassword" class="w-100b colon" label="确认密码" placeholder="请确认密码" />
      <van-field v-model="code" class="w-100b colon" label="验证码" placeholder="请输入验证码">
        <button slot="button" @click="send" class="get-code-bt">获取验证码</button>
      </van-field>
    </div>

    <div class="mg-top-20">
      <van-radio icon-size="0.9rem" class="font-9">
        我同意并勾选
        <span class="color-2D8BFF">《xx协议》</span>
      </van-radio>
    </div>

    <van-button class="register-bt radius-5" @click="reg">注册</van-button>

    <div class="tx-right">
      <router-link to="/login">
        <span class="font-9 color-2D8BFF" style="margin-right: 2.143rem;">已有账号，立即去登陆</span>
      </router-link>
    </div>
  </div>
</template>

<script>
import { register,send_message} from "../../api/api";
// import { datechange } from '../../../../maoniu2/src/router/api';
export default {
  name: "register",
  data() {
    return {
      tel: "",
      password: "",
      repassword: "",
      code: ""
    };
  },
  methods: {
    send(){
      send_message({tel:this.tel}).then(res=>{
        console.log(res)
      })
    },
    reg() {
      let data = {
        tel: this.tel,
        password: this.password,
        repassword: this.repassword,
        code: this.code
      };
      console.log(data);
      register(data).then(res => {
        console.log(res);
        if (res.data.status == 200) {
          this.$router.push({
            path: "/login"
          });
        }else{
          alert(res.data.msg)
        }
      });
    }
  },
  created(){
    console.log(111)
  }
};
</script>

<style scoped lang="less">
/deep/ .van-field__label {
  width: 3.786rem;
  font-size: 0.857rem;
  color: #656565;
  font-weight: bold;
  margin-right: 0.857rem;
}

/*注册 容器*/
.register-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 2.429rem;

  .van-cell.van-field {
    margin-top: 2.214rem;
  }

  /*获取验证码按钮*/

  .get-code-bt {
    width: 4.714rem;
    height: 1.571rem;
    text-align: center;
    color: #656565;
    line-height: 1.571rem;
    font-size: 0.786rem;
    border: 1px solid #999999;
    background-color: white;
    border-radius: 0.357rem;
  }

  /*Logo*/

  .logo-img {
    width: 6.571rem;
    height: 4.571rem;
    margin: 2.25rem auto 1.429rem;
  }

  /*注册按钮*/

  .register-bt {
    height: 2.857rem;
    line-height: 2.857rem;
    color: #282828;
    font-size: 1.071rem;
    background-color: #f4f1f4;
    border: 1px solid #9c9a9d;
    margin: 3.571rem 0.893rem 1.036rem;
  }
}
</style>
